.abs-hidden,
.abs-none,
.abs-preload {
  position: 'absolute';
  height: 0;
  width: 0;
}

.abs-cover {
  position: absolute !important;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.abs-top {
  position: absolute !important;
  top: 0;
  left: 0;
  right: 0;
  height: auto;
}

.abs-bottom {
  position: absolute !important;
  bottom: 0;
  left: 0;
  right: 0;
  height: auto;
}

.abs-left {
  position: absolute !important;
  left: 0;
  top: 0;
  bottom: 0;
  width: auto;
}

.abs-right {
  position: absolute !important;
  right: 0;
  top: 0;
  bottom: 0;
  width: auto;
}

.abs-left-top,.abs-top-left{
  position: absolute !important;
  left: 0;
  top: 0;
  width: auto;
}

.abs-right-top,.abs-top-right{
  position: absolute !important;
  right: 0;
  top: 0;
  width: auto;
}

.abs-right-bottom,.abs-bottom-right{
  position: absolute !important;
  right: 0;
  bottom: 0;
  width: auto;
}

.abs-left-bottom,.abs-bottom-left{
  position: absolute !important;
  left: 0;
  bottom: 0;
  width: auto;
}

.abs-center,.abs-center-center {
  position: absolute !important;
  right: 0;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}
